<template>
  <v-app>
    <Header v-on:tabchange="update" class="font" v-if="theme === 0" />
    <HeaderNE v-on:tabclick="update" v-on:tabchange="update" v-else-if="theme === 1" />
    <v-content style="background-color:	#008080">
  <Projects v-if="tab === 1 && theme === 0"/>
  <ProjectsNE v-on:close="tab = -1" v-if="tab === 1 && theme === 1"/>
  <Contact v-else-if="tab === 2 && theme === 0"/>
  <ContactNE v-on:close="tab = -1" v-else-if="tab === 2 && theme === 1" class="draggable"/>
  <About v-else-if="tab === 0 && theme === 0"/>
  <AboutNE v-on:close="tab = -1" v-else-if="tab === 0 && theme === 1" class="draggable"/> 
  <ProfileNE v-on:close="tab = -1" v-if="tab === 0 && theme === 1" class="draggable"/>
    </v-content>
  </v-app>
</template>

<script>
import AboutNE from "./views/AboutNE";
import ContactNE from "./views/ContactNE";
import ProjectsNE from "./views/ProjectsNE";
import HeaderNE from "./views/HeaderNE";
import ProfileNE from "./views/ProfileNE";
import interact from "interactjs";
export default {
  name: "App",

  components: {
    AboutNE,
    ContactNE,
    ProjectsNE,
    HeaderNE,
    ProfileNE,
  },

  data: () => ({
    tab: 0,
    about_open: true,
    projects_open: false,
    contact_open: false,
    theme: 1,
  }),
  methods: {
    update: function (a) {
      console.log(a);
      this.tab = a;
    },
    changeTheme: function (a) {
      this.theme = a;
      this.update(0);
    },
    initInteract: function (selector) {
      console.log("init");
      console.log(selector);
      interact(".item").draggable({
        listeners: { move: window.dragMoveListener },
        inertia: false,
        modifiers: [
          interact.modifiers.restrictRect({
            restriction: "parent",
            endOnly: true,
          }),
        ],
      });
    },
  },
  mounted: function () {
    let aD = this.$refs.aboutDraggable;
    let cD = this.$refs.contactDraggable;
    let pD = this.$refs.projectsDraggable;
    this.initInteract(aD);
    this.initInteract(cD);
    this.initInteract(pD);
  },
};
</script>

<style scoped>
.active {
  color: wheat;
  text-decoration: none;
}
.not-active {
  color: white;
}
.link-style {
  text-decoration: none;
}
.basil {
  background-color: #fffbe6 !important;
}
.basil--text {
  color: #356859 !important;
}
.font {
  font-family: "Passion One";
}
.draggable {
  touch-action: none;
}
</style>
